
'use client';

import React, { useState } from 'react';
import { RepairsList } from '@/components/dashboard/repairs-list';
import type { Repair } from '@/lib/types';
import { useUser } from '@/context/UserContext';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { ArchivedRepairsList } from '@/components/dashboard/archived-repairs-list';
import { ReadyRepairsList } from '@/components/dashboard/ready-repairs-list';
import { RepairDetailsDialog } from '@/components/dashboard/repair-details-dialog';

export default function DashboardPage() {
  const { technicians, repairs, setRepairs } = useUser();
  const [selectedRepair, setSelectedRepair] = useState<Repair | null>(null);
  const [isDetailsOpen, setIsDetailsOpen] = useState(false);
  
  const handleViewDetails = (repair: Repair) => {
    setSelectedRepair(repair);
    setIsDetailsOpen(true);
  }
  
  return (
    <>
      <div className="p-4 sm:p-6 lg:p-8 space-y-8">
        <div className="mt-6 lg:mt-8">
          <Tabs defaultValue="active">
            <TabsList>
              <TabsTrigger value="active">Active Repairs</TabsTrigger>
              <TabsTrigger value="ready">Ready</TabsTrigger>
              <TabsTrigger value="archived">Archived</TabsTrigger>
            </TabsList>
            <TabsContent value="active">
              <RepairsList repairs={repairs} setRepairs={setRepairs} technicians={technicians} onViewDetails={handleViewDetails} />
            </TabsContent>
            <TabsContent value="ready">
              <ReadyRepairsList repairs={repairs} setRepairs={setRepairs} technicians={technicians} onViewDetails={handleViewDetails} />
            </TabsContent>
            <TabsContent value="archived">
              <ArchivedRepairsList repairs={repairs} technicians={technicians} onViewDetails={handleViewDetails} />
            </TabsContent>
          </Tabs>
        </div>
      </div>
      {selectedRepair && (
        <RepairDetailsDialog
            repair={selectedRepair}
            isOpen={isDetailsOpen}
            onOpenChange={setIsDetailsOpen}
            technicians={technicians}
        />
      )}
    </>
  );
}
