
'use client';

import React from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import type { Repair, Technician } from '@/lib/types';
import { format } from 'date-fns';
import {
    Accordion,
    AccordionContent,
    AccordionItem,
    AccordionTrigger,
  } from '@/components/ui/accordion';
import { User } from 'lucide-react';


interface ArchivedRepairsListProps {
  repairs: Repair[];
  technicians: Technician[];
  onViewDetails: (repair: Repair) => void;
}

export function ArchivedRepairsList({ repairs, technicians, onViewDetails }: ArchivedRepairsListProps) {

  const getTechnicianName = (email: string) => {
    const technician = technicians.find(t => t.email === email);
    return technician ? technician.name : email;
  };

  const archivedRepairs = (repairs || []).filter(r => r.status === 'Archived');

  const repairsByTechnician = archivedRepairs.reduce((acc, repair) => {
    const techEmail = repair.assignedToName;
    if (!acc[techEmail]) {
      acc[techEmail] = [];
    }
    acc[techEmail].push(repair);
    return acc;
  }, {} as Record<string, Repair[]>);


  return (
    <Card>
      <CardHeader>
        <CardTitle>Archived Repairs</CardTitle>
        <CardDescription>
          A list of completed and archived repair jobs, grouped by technician.
        </CardDescription>
      </CardHeader>
      <CardContent>
        {Object.keys(repairsByTechnician).length > 0 ? (
            <Accordion type="multiple" className="w-full">
                 {Object.entries(repairsByTechnician).map(([technicianEmail, techRepairs]) => (
                    <AccordionItem value={technicianEmail} key={technicianEmail}>
                        <AccordionTrigger>
                            <div className='flex items-center gap-2'>
                                <User className="h-5 w-5 text-muted-foreground" />
                                <span className='text-lg font-medium'>{getTechnicianName(technicianEmail)} ({techRepairs.length} repairs)</span>
                            </div>
                        </AccordionTrigger>
                        <AccordionContent>
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Customer</TableHead>
                                        <TableHead>Device</TableHead>
                                        <TableHead className="hidden sm:table-cell">Created</TableHead>
                                        <TableHead className="hidden md:table-cell">Ready</TableHead>
                                        <TableHead className="hidden md:table-cell">Archived</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {techRepairs.map((repair) => (
                                        <TableRow key={repair.id} onClick={() => onViewDetails(repair)} className="cursor-pointer">
                                            <TableCell>
                                                <div className="font-medium">{repair.customerName}</div>
                                            </TableCell>
                                            <TableCell>{repair.brand} {repair.model}</TableCell>
                                            <TableCell className="hidden sm:table-cell">{format(repair.createdAt, 'PP')}</TableCell>
                                            <TableCell className="hidden md:table-cell">{repair.readyAt ? format(repair.readyAt, 'PP') : 'N/A'}</TableCell>
                                            <TableCell className="hidden md:table-cell">{repair.archivedAt ? format(repair.archivedAt, 'PP') : 'N/A'}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </AccordionContent>
                    </AccordionItem>
                 ))}
            </Accordion>
        ) : (
            <div className="text-center text-muted-foreground py-12">
                No archived repairs found.
            </div>
        )}
      </CardContent>
    </Card>
  );
}
